import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { User } from '../../providers';
import { SocialSharing } from '@ionic-native/social-sharing';
import {Platform } from 'ionic-angular';

/**
 * Generated class for the HelpPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-help',
  templateUrl: 'help.html',
})
export class HelpPage {

  constructor(public platform : Platform, public socialSharing: SocialSharing, public user: User, public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    //console.log('ionViewDidLoad HelpPage');
  }


  email() {
    this.platform.ready().then(() => {

    let message = 'Hola que tal\n ';
    message += 'Mi nombre es: ' + this.user.userInfo.fullname + ' \n ';
    message += 'Mi correo electronico es: ' + this.user.userInfo.email + '\n ';
    message += 'Mi celular es: ' + this.user.userInfo.cellphone + '\n ';
      message += 'Mensaje: \n \n';

    this.socialSharing.shareViaEmail(message, 'Ayuda - App Immosystem', ['soporte@immosystem.com.mx']).then(() => {

    }).catch(() => {

    });
  });
  }


  what() {

    this.platform.ready().then(() => {

      /** Compartir la propiedad por whatsapp */


      let message = 'Hola que tal\n ';
      message += 'Mi nombre es: '+ this.user.userInfo.fullname+ ' \n ';
      message += 'Mi correoelectronico es: ' + this.user.userInfo.email + '\n ';
      message += 'Mensaje: \n ';
      this.socialSharing.shareViaWhatsAppToReceiver('+52 9983401956',message).then(() => {
        // Success!
      }).catch(() => {
        // Error!
      });
    });


  }

}
 


